/** @format */

import React, { Fragment } from "react";
import "../assets/css/style.css";
import host from "../hosting/host";
function Login({ onCheckDangNhap }) {
  const [Data, SetData] = React.useState([
    {
      TaiKhoan: "",
      MatKhau: "",
    },
  ]);
  const [error, Seterror] = React.useState("");
  const OnClickDangNhap = async () => {
    try {
      if (Data[0].TaiKhoan === "") {
        Seterror("Bạn chưa nhập tài khoản");
      } else if (Data[0].MatKhau === "") {
        Seterror("Bạn chưa nhập mât khẩu");
      } else {
        var url = host.DangNhap + `/${Data[0].TaiKhoan}/${Data[0].MatKhau}`;
        const response = await fetch(url);
        const JsonData = await response.json();
        console.log(JsonData);
        if (JsonData.length > 0) {
          Seterror("Đăng nhập thành công");
          onCheckDangNhap(true);
        } else {
          Seterror("Đăng nhập thất bại");
          onCheckDangNhap(false);
        }
      }
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <Fragment>
      <div className='d-flex justify-content-center'>
        <div className='login-page'>
          <div className='form'>
            <div className='login-form'>
              <input
                type='text'
                placeholder='Tài khoản'
                onChange={(e) => (Data[0].TaiKhoan = e.target.value)}
                onKeyUp={(e) => {
                  if (e.key === "Enter") OnClickDangNhap();
                }}
              />
              <input
                type='password'
                placeholder='Mật khẩu'
                onChange={(e) => (Data[0].MatKhau = e.target.value)}
                onKeyUp={(e) => {
                  if (e.key === "Enter") OnClickDangNhap();
                }}
              />
              <button onClick={OnClickDangNhap}>Đăng nhập</button>
              <p
                className='mt-3'
                style={
                  error === "Đăng nhập thành công"
                    ? { color: "green" }
                    : { color: "red" }
                }>
                {error}
              </p>
              {/* <p className='message'>
              Not registered? <a href='#'>Create an account</a>
            </p> */}
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default Login;
