import React, { Fragment,useState } from 'react'
import Main from './views/Main'
import Login from './views/Login'
function App() {
  const [onCheck, setonCheck] = useState(false)
  const onCheckDangNhap =(e)=>{
    console.log(e)
    setonCheck(e)
  }
  if(onCheck){
    alert('Đăng nhập thành công !')
    return(
        <Main></Main>
    )
  }
  return (
    <Fragment>
        <Login onCheckDangNhap={onCheckDangNhap}></Login>
    </Fragment>
  )
}

export default App
