import React from "react";
import "../styles.css";
import firebase from "../firebase";
import MapGoogle from '../views/MapGoogle'

export default function Main() {

  const [_lat,Setlat] = React.useState(0)
  const [_lng,Setlng] = React.useState(0)
  const style = {"cursor":"pointer","width":"100px","height":"100px","backgroundColor":"rgb(80, 16, 199)","borderRadius":"25px","color":"white"}
  const DieuKhienXe = (string)=>{
    var updates = {};
    console.log(string)
    updates['DieuKhienXe/ThaoTac'] =  string;
    var starCountRef = firebase.ref().update(updates);
  }
  const GetData = ()=>{
    firebase.ref('DinhVi/_lat').on('value', function (snapshot) {
      Setlat(snapshot.val())
  });
  firebase.ref('DinhVi/_lng').on('value', function (snapshot) {
    Setlng(snapshot.val())
});
  }
  React.useEffect(()=>{
    GetData()
  },[])

  return (
    // secondary
    <div className="container">
    <div className="App">
        <h1 style={{color:'white'}}>Điều khiển xe</h1>
          <div className="d-flex justify-content-center">
          <table className="">
            <thead>

            </thead>
            <tr>
                <button style={style}>{'-'}</button>
                <button style={style} onClick={async ()=> DieuKhienXe('up')}>{'UP'}</button>
                <button style={style}>{'-'}</button>
            </tr>
            <tr>
                <button style={style} onClick={async ()=> DieuKhienXe('left')}>{'LEFT'}</button>
                <button style={style}>{'0'}</button>
                <button style={style} onClick={async ()=> DieuKhienXe('right')}>{'RIGHT'}</button>
            </tr>
            <tr>
                <button style={style}>{'-'}</button>
                <button style={style} onClick={async ()=> DieuKhienXe('down')}>{'DOWN'}</button>
                <button style={style}>{'-'}</button>
            </tr>
            <tr>
              <button onClick={async ()=> DieuKhienXe('auto')} style={{"textAlign":"center","width":"100px","height":"100px","backgroundColor":"rgb(80, 16, 199)","borderRadius":"25px","color":"white"}}>{'AUTO'}</button>
              <button onClick={async ()=> DieuKhienXe('stop')} style={{"textAlign":"center","width":"100px","height":"100px","backgroundColor":"rgb(80, 16, 199)","borderRadius":"25px","color":"white"}}>{'STOP'}</button>
              <button onClick={async ()=> DieuKhienXe('dieukhiengan')} style={{"textAlign":"center","width":"100px","height":"100px","backgroundColor":"rgb(80, 16, 199)","borderRadius":"25px","color":"white"}}>{'XE ĐK Gần'}</button>
            </tr>
        </table>
          </div>

    </div>
    <MapGoogle _lat={parseFloat(_lat)} _lng={parseFloat(_lng)}></MapGoogle>
    </div>
    
  );
}
