import React, { Component,Fragment } from 'react';
import GoogleMapReact from 'google-map-react';
import host from '../hosting/host'
const AnyReactComponent = ({ text }) => (
    <Fragment>
        <i className="fas fa-map-marker"></i>
<div style={{color:"red",fontSize:"19px"}}>{text}</div>
    </Fragment>
)


function SimpleMap({_lat,_lng}) {
    const [__lat,Set_lat] = React.useState(_lat)
    const [__lng,Set_lng] = React.useState(_lng)
    const defaultProps = {
        center: {
          lat: __lat,
          lng: __lng
        },
        zoom: 1
      };
      React.useEffect(()=>{
        Set_lat(_lat)
        Set_lng(_lng)
      },[_lat,_lng])
      const LuuGiaTriHienTai = async()=>{
        try {
            const response = await fetch(host.LuuDinhVi,{
                method:'POST',
                headers: { 'Content-Type': 'application/json' },
                body:JSON.stringify({__lat,__lng})
            })
        } catch (error) {
          
        }
      }
    return (
<div style={{ height: '100vh', width: '100%' }} className="mt-3">
    <h5 style={{color:'white'}}>Lat : {_lat}</h5>
    <h5 style={{color:'white'}}>Lng : {_lng}</h5>
    <button className="btn btn-primary mb-3" onClick={LuuGiaTriHienTai}>Lưu giá trị hiện tại</button>
        <GoogleMapReact
          bootstrapURLKeys={{ key: 'AIzaSyBSwv46bwz66HEJzUht0YRZ-nX4mkpkc44' }}
          defaultCenter={defaultProps.center}
          defaultZoom={defaultProps.zoom}
        >
          <AnyReactComponent
            lat={__lat}
            lng={__lng}
            text="Xe"
          />
        </GoogleMapReact>
      </div>
    )
}
export default SimpleMap